"use client"

import { cn } from "@/lib/utils"
import type { AgentState } from "@/lib/store"
import {
  Sparkles,
  Package,
  CreditCard,
  Truck,
  Gift,
  HeadphonesIcon,
  Loader2,
  CheckCircle2,
  XCircle,
} from "lucide-react"

const agentIcons: Record<string, typeof Sparkles> = {
  recommendation: Sparkles,
  inventory: Package,
  payment: CreditCard,
  fulfillment: Truck,
  loyalty: Gift,
  postPurchase: HeadphonesIcon,
}

const statusColors: Record<string, string> = {
  idle: "bg-[#7A0C1B] text-[#F6D365]",
  running: "bg-[#B11226]/40 text-[#F2B705] border border-[#F2B705]/40",
  success: "bg-green-700/30 text-green-400 border border-green-500/30",
  error: "bg-red-700/30 text-red-400 border border-red-500/30",
}

interface AgentTileProps {
  agentKey: string
  agent: AgentState
  compact?: boolean
}

export function AgentTile({ agentKey, agent, compact = false }: AgentTileProps) {
  const Icon = agentIcons[agentKey] || Sparkles

  return (
    <div
      className={cn(
        compact
          ? "flex items-center gap-2 px-3 py-1 rounded-full text-xs font-medium"
          : "flex flex-col gap-2 p-4 rounded-lg",
        statusColors[agent.status],
      )}
    >
      <div className="flex items-center gap-2">
        {agent.status === "running" && <Loader2 className="h-4 w-4 animate-spin" />}
        {agent.status === "success" && <CheckCircle2 className="h-4 w-4" />}
        {agent.status === "error" && <XCircle className="h-4 w-4" />}
        {agent.status === "idle" && <Icon className="h-4 w-4" />}
        <span className="font-medium">{agent.name}</span>
      </div>
    </div>
  )
}
