"use client"

import { useState, useRef, useEffect } from "react"
import { useStore } from "@/lib/store"
import { cn } from "@/lib/utils"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { ChannelSwitcher } from "@/components/channel-switcher"
import { AgentTile } from "@/components/agent-tile"
import { Send, Mic, User, Bot, Loader2 } from "lucide-react"

export function ChatShell() {
  const { state } = useStore()
  const [input, setInput] = useState("")
  const messagesEndRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" })
  }, [state.messages])

  return (
    <div className="flex flex-col h-full bg-[#4A0E12] text-[#F6D365]">
      <div className="p-4 border-b border-[#B11226]/40">
        <h2 className="text-lg font-semibold text-[#F2B705]">Birla OneAI Assistant</h2>
        <ChannelSwitcher />
      </div>

      <div className="flex-1 overflow-y-auto p-4 space-y-4">
        {state.messages.map((m) => (
          <div
            key={m.id}
            className={cn(
              "flex gap-3 max-w-[80%]",
              m.role === "user" ? "ml-auto flex-row-reverse" : "",
            )}
          >
            <div
              className={cn(
                "h-8 w-8 rounded-full flex items-center justify-center",
                m.role === "user" ? "bg-[#B11226]" : "bg-[#7A0C1B]",
              )}
            >
              {m.role === "user" ? <User className="h-4 w-4" /> : <Bot className="h-4 w-4" />}
            </div>
            <div
              className={cn(
                "px-4 py-2 rounded-2xl text-sm",
                m.role === "user"
                  ? "bg-[#B11226] text-[#F6D365]"
                  : "bg-[#7A0C1B] text-[#F6D365]",
              )}
            >
              {m.content}
            </div>
          </div>
        ))}
        <div ref={messagesEndRef} />
      </div>

      <div className="p-4 border-t border-[#B11226]/40 flex gap-2">
        <Input
          value={input}
          onChange={(e) => setInput(e.target.value)}
          placeholder="Type your message..."
          className="bg-[#7A0C1B] border-[#B11226]/40 text-[#F6D365]"
        />
        <Button className="bg-[#B11226] hover:bg-[#7A0C1B]">
          <Send className="h-4 w-4" />
        </Button>
      </div>
    </div>
  )
}
